
  import ImageUpload from "../../common/userBgUpload.vue"

  import Toast from 'vux/src/components/toast/index.vue'


  export default {
    data() {
      return {
        baseUrl: M.baseImgUrl,
        showChangeBgMask: false,
        userUrl: '',
        imageUploadData: {
//          image: [], //数据格式: {"url": "xxxxx", "id": 123}
          onlyImage: {},
          isGetImageId: false  //是否要取id
        },
        uploadingFlag: {
          uploading: false,
          uploadingSuccess: false
        },
        userObj: {
          userId: 0,
          nickName: '',
          headImg: {url: ''},
          introduction: '',
          bgImg: {url: ''}
        }
      }
    },
    methods: {
      goEditPage(){
        var url = window.location.origin + '/admin/#/admin/punch?type=attendance';
        window.location.href = url;
      },
      stop(e){
        e.stopPropagation();
        e.preventDefault();
        return false
      },
      goToPage(urlValue){
        if (urlValue == "/admin/home-getInfoFromUrl") {
          var _self = this;
          _self.H.toAjax(true).then(function (data) { //data 公用参数
            var submitData = data;
            _self.A.post("/v1/userbind/userCode", submitData).then(function (result) {
              console.info(result);
              if(result.data.state.code == 0){
                return result.data.data.userCode;
              } else {
                _self.$emit("modalAlert", result.data.state.msg, "确定");
              }
            }).then(function (userCode) {
              submitData.data = {
                userCode: userCode
              };
              _self.A.post("/v1/userbind/getQcAndName", submitData).then(function (result) {
                console.info(result);
                if (result.data.state.code == 0) {
                  console.info(result.data.data);
                  //如果已经设置过默认分享方式,直接到选择页面
                  if(result.data.data.appName || result.data.data.appQrImg) {
                    _self.$router.push({"path": "/admin/home-setShareType", query: {userCode: userCode}});
                  }
                  //否则让用户输入一篇文章获取再选择
                  else {
                    _self.$router.push({"path": "/admin/home-getInfoFromUrl", query: {userCode: userCode}});
                  }
                  return

                } else {
                  _self.$emit("modalAlert", result.data.state.msg, "确定");
                }
              });
            });

          });
        } else {
          this.$router.push(urlValue);
        }
      },
      doSubmitBg(){
        //alert(222);
      }
    },
    components: {
      ImageUpload: ImageUpload,
      Toast
    },
    mounted(){
      document.getElementById("title").innerHTML = "我的";
      var userId = M.user.userId;
      !userId ? userId = 0 : userId = userId;
      var _self = this;
      _self.H.toAjax(true).then(function (data) { //data 公用参数
        var submitData = data;
        submitData.data = {
          userId: userId,
        };
        _self.A.post("/v1/user/get", submitData).then(function (result) {
          console.info(result)
          if (result.data.state.code == 0) {
            var userObj = result.data.data.user;
            console.log(userObj);
            if (result.data.data.user.bgImg == null) {
              result.data.data.user.bgImg = {url: ''}
            }
            _self.as.extend(true, _self.userObj, userObj);
            console.info(userObj);
          } else {
            _self.$emit("modalAlert", result.data.state.msg, "确定");
          }
        });

        _self.A.post("/v1/user/userUrl", submitData).then(function (result) {
          console.info(result);
          if (result.data.state.code == 0) {
            console.log(result.data.data.userUrl);
            _self.userUrl = result.data.data.userUrl;
          } else {
            _self.$emit("modalAlert", result.data.state.msg, "确定");
          }
        });
      });
    },
    watch: {
      'imageUploadData.onlyImage'(val){
        console.log(val);
        this.uploadingFlag.uploading = true;
        if (val.url) {
          this.userObj.bgImg.url = val.url;
          var _self = this;
          _self.H.toAjax(true).then(function (data) { //data 公用参数
            var submitData = data;
            submitData.data = {
              image: _self.imageUploadData.onlyImage,
              type: 'bg'
            };
            _self.A.post("/v1/common/uploadImageUpdateEntity", submitData).then(function (result) {
              console.log(result);
              _self.uploadingFlag.uploading = false;
              _self.uploadingFlag.uploadingSuccess = true;
            }).catch(function () {
              _self.uploadingFlag.uploading = false;
              _self.uploadingFlag.uploadingSuccess = true;
            });
          });
        }
      }
    }
  }



